.App {
  text-align: center;
  font-weight: 700;
  font-size: 16pt;
  background-color: green;
  top:0; bottom: 0; position: absolute;
  left:0; right:0;
  color: gold;
}

body { 
  background-color: green;
}

button {
  font-weight: 800;
  color: gold;
  background-color: green;
  border: 8px double gold;
  font-size: 24pt;
  padding: 12px 24px;
  cursor: pointer;
  text-shadow: 1px 2px rgba(0,0,0,.5);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
